import React, { memo, useEffect, useState } from "react";

import styled from "styled-components";
import mq from "../../components/MediaQuery";

/** 리덕스 관련 */
import { useSelector, useDispatch } from "react-redux";
// Slice에 정의된 함수 참조
import { getList } from "../../slices/NewMemberSlice";

/** 컴포넌트 관련 */
import Spinner from "../../components/Spinner";
import NewMemberWeek from "./NewMemberWeek";
import NewMemberMonth from "./NewMemberMonth";

const NewMemberContainer = styled.div`
    width: 48%;
    margin: 2rem auto;
    padding: 2rem;
    background-color: #ffffff;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    
    .selectBox-container {
        margin-bottom: 2rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        h2 {
            font-size: 1.5rem;
            font-weight: 600;
            color: #333;
            margin: 0;
        }
    }

    .selectBox {
        width: 200px;
        padding: 10px 15px;
        font-size: 1rem;
        border: 1px solid #e0e0e0;
        border-radius: 6px;
        background-color: #fff;
        cursor: pointer;
        transition: all 0.2s ease-in-out;

        &:hover {
            border-color: #2196f3;
        }

        &:focus {
            outline: none;
            border-color: #2196f3;
            box-shadow: 0 0 0 2px rgba(33, 150, 243, 0.2);
        }
    }
    
    ${mq.maxWidth("xl")`
        width: 90%;
        margin: 1rem auto;
        padding: 1rem;
    `}
`;

const NewMember = memo(() => {
  /** 기본 데이터 처리 */
  const { loading, status, message, weekly, monthly } = useSelector((state) => state.NewMemberSlice);

  // 리덕스 스토어에 정의된 함수를 사용하기 위한 준비
  const dispatch = useDispatch();

  // 그래프 선택
  const [selectedGraph, setSelectedGraph] = useState("week");

  // 그래프 변경
  const graphChange = (e) => {
    setSelectedGraph(e.target.value);
  };

  // 컴포넌트가 생성되면 실행되는 함수
  useEffect(() => {
    dispatch(getList());
  }, []);

  return (
    <NewMemberContainer>
      <Spinner loading={loading} />
     
      {/* {weekly && (<p>{JSON.stringify(weekly)}</p>)} */}
      {/* {monthly && (<p>{JSON.stringify(monthly)}</p>)} */}
      {/* <label>
        <input type="radio" value="week" checked={selectedGraph === "week"} onChange={graphChange} />
        주간
      </label>
      <label>
        <input type="radio" value="month" checked={selectedGraph === "month"} onChange={graphChange} />
        월간
      </label> */}
    <div className="selectBox-container">
    <h2>신규 회원</h2>
      <select value={selectedGraph} onChange={graphChange} className="selectBox">
        <option value="week">주간 신규회원</option>
        <option value="month">월간 신규회원</option>
      </select>
    </div>

      {selectedGraph === "week" && <NewMemberWeek />}
      {selectedGraph === "month" && <NewMemberMonth />}
    </NewMemberContainer>
  );
});

export default NewMember;
