import React, { memo, useMemo } from "react";
import { useSelector } from "react-redux";

import styled from "styled-components";

import dayjs from "dayjs";

import { Chart, CategoryScale, LinearScale, PointElement, Title, Tooltip, Legend, LineElement } from "chart.js";

import { Line } from "react-chartjs-2";

Chart.register(CategoryScale, LinearScale, PointElement, Title, Tooltip, Legend, LineElement);

const NewMemberWeekContainer = styled.div`

  .container {
    height: 500px;
  }
`;

const NewMemberWeek = memo(() => {
  // 데이터를 가져온다.
  const { weekly } = useSelector((state) => state.NewMemberSlice);

  // 데이터를 가공한다.
  const { date, memberCount } = useMemo(() => {
    // 데이터가 없으면 빈 배열을 반환한다.
    if (!weekly) {
      return { date: [], memberCount: [] };
    }

    const date = [];
    const memberCount = [];

    weekly.forEach((v, i) => {
      date[i] = `${v.date}\n${v.day}`;
      memberCount[i] = v.memberCount;
    });

    date.reverse();
    memberCount.reverse();

    const result = { date, memberCount };

    return result;
  }, [weekly]);

  return (
    <NewMemberWeekContainer>
      <div className="container">
        {/* {date && JSON.stringify(date)} */}
        {/* {memberCount && JSON.stringify(memberCount)} */}

        {date && memberCount && (
          <Line
            data={{
              labels: date,
              datasets: [
                {
                  label: "명",
                  data: memberCount,
                  backgroundColor: "rgba(55, 162, 235, 0.5)",
                  borderColor: "rgba(55, 162, 235, 1 )",
                  borderWidth: 1,
                },
              ],
            }}
            options={{
              responsive: true,
              maintainAspectRatio: false,
              plugins: {
                legend: {
                  position: "left",
                },
                title: {
                  display: true,
                  text: "주간 일별 신규 회원",
                  font: {
                    size: 16,
                    color: "#000",
                  },
                },
              },
            }}
          />
        )}
      </div>
    </NewMemberWeekContainer>
  );
});

export default NewMemberWeek;
