import React, { memo, useMemo } from "react";
import { useSelector } from "react-redux";

import styled from "styled-components";


import dayjs from "dayjs";

import { Chart, CategoryScale, LinearScale, PointElement, Title, Tooltip, Legend, LineElement } from "chart.js";

import { Line } from "react-chartjs-2";

Chart.register(CategoryScale, LinearScale, PointElement, Title, Tooltip, Legend, LineElement);

const NewMemberMonthContainer = styled.div`
  .container {
    height: 500px;
    
  }
`;

const NewMemberMonth = memo(() => {
  // 데이터를 가져온다.
  const { monthly } = useSelector((state) => state.NewMemberSlice);

  // 데이터를 가공한다.
  const { date, memberCount } = useMemo(() => {
    // 데이터가 없으면 빈 배열을 반환한다.
    if (!monthly) {
      return { date: [], memberCount: [] };
    }

    const date = [];
    const memberCount = [];

    monthly.forEach((v, i) => {
      date[i] = v.week;
      memberCount[i] = v.memberCount;
    });

    date.reverse();
    memberCount.reverse();

    const result = { date, memberCount };

    return result;
  }, [monthly]);

  return (
    <NewMemberMonthContainer>
      <div className="container">
        {date && memberCount && (
          <Line
            data={{
              labels: date,
              datasets: [
                {
                  label: "명",
                  data: memberCount,
                  backgroundColor: "rgba(61, 111, 218, 0.6)",
                  borderColor: "rgb(61, 111, 218)",
                  borderWidth: 1,
                },
              ],
            }}
            options={{
              responsive: true,
              maintainAspectRatio: false,
              plugins: {
                legend: {
                  position: "left",
                },
                title: {
                  display: true,
                  text: "월간 주별 신규 회원",
                  font: {
                    size: 16,
                    color: "#000",
                  },
                },
              },
            }}
          />
        )}
      </div>
    </NewMemberMonthContainer>
  );
});

export default NewMemberMonth;
