import React, {memo, useMemo} from "react";
import { useSelector } from "react-redux";

import styled from "styled-components";

import dayjs from "dayjs";

import {
    Chart, CategoryScale, LinearScale, PointElement, Title, Tooltip, Legend,
    LineElement
} from "chart.js";

import { Line } from "react-chartjs-2";

Chart.register(CategoryScale, LinearScale, PointElement, Title, Tooltip, Legend, LineElement);

const SalesWeekGraphContainer = styled.div`
    .container {
        height: 500px;
    }
`;

const SalesWeekGraph = memo(() => {
    const { item } = useSelector((state) => state.SalesSlice);
    
    const { date, totalSales } = useMemo(() => {
        if (!item) {
            return {date: [], totalSales: []}
        }

        const day = dayjs();
        const now = day.format("YYYY-MM-DD");

        const date = [];
        const totalSales = [];

        item.forEach((v, i) => {     
            if (v.date < now && day.diff(dayjs(v.date), "day") <= 7) {
                date[i] = v.date;
                totalSales[i] = v.totalSales;
            }
        });

        date.reverse();
        totalSales.reverse();
        const result = { date, totalSales }

        return result;
    }, [item]);

    return (
        <SalesWeekGraphContainer>
            <div className="container">
                {/* {date && JSON.stringify(date)} */}
                {/* {totalSales && JSON.stringify(totalSales)} */}

                {date && totalSales && (
                    <Line 
                        data={{
                            labels: date,
                            datasets: [
                                {
                                    label: "원",
                                    data: totalSales,
                                    backgroundColor: 'rgba(255, 99, 132, 0.6)',
                                    borderColor: 'rgba(255, 99, 132, 1)',
                                    borderWidth: 1,
                                }
                            ]
                        }}

                        options={{
                            responsive: true,
                            maintainAspectRatio: false,
                            plugins: {
                                legend: {
                                    position: "left",
                                },
                                title: {
                                    display: true,
                                    text: "주간 일별 총 매출액",
                                    font: {
                                        size: 16,
                                        color: "#000",
                                    }
                                }
                            }
                        }}
                    />
                )}
            </div>
        </SalesWeekGraphContainer>
    );
});

export default SalesWeekGraph;