import React, {memo, useEffect, useState} from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { getList } from '../../slices/SalesSlice';

import mq from '../../components/MediaQuery';

import styled from 'styled-components';
import SalesWeekGraph from './SalesWeekGraph';
import SalesMonthGraph from './SalesMonthGraph';
import Spinner from '../../components/Spinner';

const SalesContainer = styled.div`
    width: 48%;
    margin: 2rem auto;
    padding: 2rem;
    background-color: #ffffff;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    
    .selectBox-container {
        margin-bottom: 2rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        h2 {
            font-size: 1.5rem;
            font-weight: 600;
            color: #333;
            margin: 0;
        }
    }

    .selectBox {
        width: 200px;
        padding: 10px 15px;
        font-size: 1rem;
        border: 1px solid #e0e0e0;
        border-radius: 6px;
        background-color: #fff;
        cursor: pointer;
        transition: all 0.2s ease-in-out;

        &:hover {
            border-color: #2196f3;
        }

        &:focus {
            outline: none;
            border-color: #2196f3;
            box-shadow: 0 0 0 2px rgba(33, 150, 243, 0.2);
        }
    }
  
    ${mq.maxWidth("xl")`
        width: 90%;
        margin: 1rem auto;
        padding: 1rem;
    `}
`;

const Sales = memo(() => {
    const { loading, status, message, item } = useSelector((state) => state.SalesSlice);

    const [selectGraph, setSelectGraph] = useState("week");

    const onGraphSelect = (e) => {
        setSelectGraph(e.currentTarget.value);
    }

    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(getList());
    }, []);

    return (
        <SalesContainer>

            <Spinner loading={loading} />

            {/* {item && (<p>{JSON.stringify(item)}</p>)} */}
            <div className="selectBox-container">
                <h2>매출</h2>
                <select onChange={onGraphSelect} className="selectBox">
                    <option value="week">주간 매출</option>
                    <option value="month">월간 매출</option>
                </select>
            </div>
            {selectGraph == "week" && <SalesWeekGraph />}
            {selectGraph == "month" && <SalesMonthGraph />}
        </SalesContainer>
    );
});

export default Sales;