import React, {memo, useMemo} from "react";
import { useSelector } from "react-redux";

import styled from "styled-components";

import dayjs from "dayjs";

import {
    Chart, CategoryScale, LinearScale, PointElement, Title, Tooltip, Legend,
    LineElement
} from "chart.js";

import { Line } from "react-chartjs-2";

Chart.register(CategoryScale, LinearScale, PointElement, Title, Tooltip, Legend, LineElement);

const SalesMonthGraphContainer = styled.div`
    .container {
        height: 500px;
    }
`;

const SalesMonthGraph = memo(() => {
    const { item } = useSelector((state) => state.SalesSlice);
    
    const { date, totalSales } = useMemo(() => {
        if (!item) {
            return {date: [], totalSales: []}
        }

        console.log(item);

        const date = [];
        const totalSales = [];

        let count = 0;
        let sum = 0;
        item.forEach((v, i) => {      
                
            if ((i + 1) % 7 == 0) {
                totalSales[count] = sum + v.totalSales;
                count++;
                sum = 0;
            } else {
                sum += v.totalSales;
            }
        });

        totalSales.reverse();

        const result = { date, totalSales }
        return result;
    }, [item]);
    return (
        <SalesMonthGraphContainer>
            <div className="container">
            {date && totalSales && (
                    <Line 
                        data={{
                            labels: ["3주전", "2주전", "1주전", "이번주"],
                            datasets: [
                                {
                                    label: "원",
                                    data: totalSales,
                                    backgroundColor: 'rgba(223, 99, 42, 0.6)',
                                    borderColor: 'rgba(223, 99, 42, 1)',
                                    borderWidth: 1,
                                }
                            ]
                        }}

                        options={{
                            responsive: true,
                            maintainAspectRatio: false,
                            plugins: {
                                legend: {
                                    position: "left",
                                },
                                title: {
                                    display: true,
                                    text: "월간 주별 총 매출액",
                                    font: {
                                        size: 16,
                                        color: "#000",
                                    }
                                }
                            }
                        }}
                    />
                )}
            </div>
        </SalesMonthGraphContainer>
    );
});

export default SalesMonthGraph;